'use strict';
angular.module('core.utils').controller('FooterCtrl', /*@ngInject*/ function controller($scope, $timeout, $mdDialog, $state, Contact, Share, myContact) {
    var vm = this;

    // 
    // Contact
    // 
    vm.contact = new Contact.service();
    vm.submitContact = submitContact;

    vm.openForm = openForm;
    vm.myContactFac = myContact;
    vm.myContacts = false;
    getContacts();

    //
    // Esconder/mostrar botões ao rolar
    //
    var scrollElem = $("md-content.layout-content"),
        btnTop = scrollElem.find('#btn-speed-top'),
        btnContact = scrollElem.find('#btn-speed-contact'),
        timer = false;


    scrollElem.on('scroll', function() {
        var scroll = $(this);

        // 
        // Cancelar timeout anterior
        // 
        $timeout.cancel(timer);

        timer = $timeout(function() {
            // 
            // Posição da janela, div de contato e distancia entre os dois
            // 
            var position = scroll.scrollTop(),
                divContactPos = scrollElem.find('#contato').position().top;

            // console.log('position', position);
            // console.log('divContactPos', divContactPos);

            // 
            // Esconder mostrar botão top de acordo com posição do topo
            // 
            if (position > 100) {
                btnTop.fadeIn();
            } else {
                btnTop.fadeOut();
            }

            // 
            // Esconder / mostrar botão de contato de acordo com a distancia
            // 
            if (divContactPos < 350) {
                btnContact.fadeOut();
            } else {
                btnContact.fadeIn();
            }
        }, 500);
    });

    // 
    // Code
    // 
    function submitContact() {

        // Send contact to server with recaptcha
        vm.contact.create().then(success, fail);

        function success(response) {
            $state.go('app.home.obrigado');

            // console.log('response', response);
            // console.log('sucesso');
        }

        function fail(response) {
            // console.log('response', response);
            // console.log('falhou');
        }
    }

    function openForm(ev) {
        $mdDialog.show({
            controller: DialogController,
            templateUrl: 'app/home/indicacao-form.tpl.html',
            parent: angular.element(document.body),
            targetEvent: ev,
            clickOutsideToClose: true
        });
    }

    function DialogController($scope, $mdDialog, Share) {
        $scope.hide = hide;
        $scope.share = new Share.service();
        $scope.submit = submit;

        function hide() {
            $mdDialog.hide();
        };

        function submit() {
            // Send contact to server with recaptcha
            $scope.share.create().then(success, fail);

            function success(response) {
                // 
                // Fechar 
                // 
                hide();

                // console.log('response', response);
                // console.log('enviado com sucesso');
            }

            function fail(response) {
                // console.log('response', response);
                // console.log('falhou');
            }
        }
    }

    function submitNews() {

        // Send contact to server with recaptcha
        vm.news.create().then(success, fail);

        function success(response) {
            //
            // Enviar para página tabela de valores
            // 
            $state.go('app.valores');

            $scope.$on('$stateChangeSuccess', function(ev) {
                $timeout(function() {
                    $("md-content.layout-content").animate({
                        scrollTop: 0
                    }, 100);
                }, 1000)
            });

            // console.log('response', response);
            // console.log('enviado com sucesso');
        }

        function fail(response) {
            // console.log('response', response);
            // console.log('falhou');
        }
    }

    function getContacts() {
        vm.myContactFac.get().then(function(data) {
            vm.myContacts = data;
        });
    }
});
