(function() {
    'use strict';

    angular
        .module('core.app')
        .directive('acmeNavbar', acmeNavbar);

    /** @ngInject */
    function acmeNavbar() {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/layout/navbar/navbar.tpl.html',
            controller: NavbarController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function NavbarController($element, $mdSidenav, $state) {
            var vm = this;

            vm.sidenav = $mdSidenav;
            vm.currentState = $state.current.name;

            //
            // scrollTo
            //
            vm.scrollTo = scrollTo;

            function scrollTo(view) {
                var target = $('#' + view);
                if (target.length) {
                    $("md-content.layout-content").animate({
                        scrollTop: target.offset().top
                    }, 800, 'swing');
                }
            }

            // "vm.creation" is avaible by directive option "bindToController: true"
            // vm.relativeDate = moment(vm.creationDate).fromNow();

            //
            // Fixed on scroll
            // 
            var wdow = $("md-content.layout-content"),
                currentState = false;

            // Bind window scroll
            wdow.bind('scroll', navbarPosition);

            // Fix / unfix navbar
            function navbarPosition() {
                var elem = $('md-toolbar.toolbar-default');
                var point = $(".fixed-point");
                var pos = point.offset().top;

                if (currentState === false && pos <= 0) {
                    currentState = 'add';
                    elem.hide().slideDown('slow').addClass('fixed');
                } else if (currentState === 'add' && pos > 0 ) {
                    currentState = false;
                    elem.hide().show().removeClass('fixed');
                }
            }
        }
    }

})();
