(function() {
    'use strict';
    angular.module('core.admin.step').service('Step', /*@ngInject*/ function($http, $page, $state, $q, api, Busy, NegativePromise) {

        var url = api.url + '/api/steps/';
        this.service = service;

        function service(params) {
            //
            // Default Params
            // 
            this.busy = new Busy.service();
            this.model = null;

            //
            // Instantiate
            // 
            params = params ? params : {};
            angular.extend(this, params);

        }

        this.service.prototype.update = update;
        this.service.prototype.formStatus = formStatus;

        function update() {
            // Are we busy?
            if (this.busy.start('create')) return NegativePromise.defer();

            //
            // Limpar o model e separar outros dados
            // 
            var model = this.model;
            
            return $http
                .put(url + model.id, {
                    model: model
                })
                .success(
                    //
                    // success
                    //
                    function(response) {
                        $page.toast('Etapas atualizadas com sucesso!');
                        this.busy.end('create');
                        
                        return response;
                    }.bind(this))
                .error(
                    //
                    // fail
                    //
                    function(response) {
                        $page.toast(response && response.error ? response.error : 'não foi possível atualizar as etapas, tente novamente!');
                        this.busy.end('create');

                        return response;
                    }.bind(this));
        }

        function formStatus(method, invalid) {
            return (this.busy.status(method));
        }
    });
})();
