'use strict';
angular.module('core.admin.step').factory('step', /*@ngInject*/ function($http, $q, $page, api, Busy, NegativePromise) {
    var endpoint = api.url + '/api/steps/',
        busy = new Busy.service();

    return {
        get: get,
        busy: busy
    }

    function get() {
        // Are we busy?
        if (this.busy.start('get')) return NegativePromise.defer();

        return $http.get(endpoint).then(onSuccess.bind(this)).catch(onError.bind(this));

        //handle unlink success
        function onSuccess(response) {
            this.busy.end('get');
            return response.data;
        }

        //handle unlink fail
        function onError(response) {
            $page.toast(response.error ? response.error : 'não foi possível selecionar as etapas');
            this.busy.end('get');
        }
    }
});
