'use strict';
angular.module('core.admin.slider').factory('slider', /*@ngInject*/ function($http, $q, $page, api, Busy, NegativePromise) {
    var endpoint = api.url + '/api/sliders/',
        getAllParams = {
            user: false,
            page: 0,
            limit: 1,
            filters: false
        },
        getParams = {
            user: false
        },
        busy = new Busy.service();

    return {
        getAll: getAll,
        get: get,
        remove: remove,
        querySearch: querySearch,
        busy: busy
    }

    function getAll(queryParams) {
        // Are we busy?
        if (this.busy.start('sliderGetAll')) return NegativePromise.defer();

        // Merge params
        angular.extend(getAllParams, queryParams);

        return $http.get(endpoint, {
            params: getAllParams
        }).then(onSuccess.bind(this)).catch(onError.bind(this));

        //handle unlink success
        function onSuccess(response) {
            this.busy.end('sliderGetAll');
            return response.data;
        }

        //handle unlink fail
        function onError(response) {
            $page.toast(response.error ? response.error : 'não foi possível selecionar os sliders');
            this.busy.end('sliderGetAll');
        }
    }

    function get(id, queryParams) {
        // Are we busy?
        if (this.busy.start('sliderGet')) return NegativePromise.defer();

        // Merge params
        angular.extend(getParams, queryParams);

        return $http.get(endpoint + id, {
            params: getParams
        }).then(onSuccess.bind(this)).catch(onError.bind(this));

        //handle unlink success
        function onSuccess(response) {
            this.busy.end('sliderGet');
            return response.data;
        }

        //handle unlink fail
        function onError(response) {
            $page.toast(response.error ? response.error : 'não foi possível selecionar o slider');
            this.busy.end('sliderGet');
        }
    }

    function remove(e, entry) {
        // Are we busy?
        if (this.busy.start('sliderDelete')) return NegativePromise.defer();

        return $http.delete(endpoint + entry.id).then(onSuccess.bind(this)).catch(onError.bind(this));

        //handle unlink success
        function onSuccess(response) {
            this.busy.end('sliderDelete');
            return response;
        }

        //handle unlink fail
        function onError(response) {
            $page.toast(response.error ? response.error : 'não foi possível deletar o slider');
            this.busy.end('sliderDelete');
        }
    }

    function querySearch(query) {
        if (!query) return false;

        var deferred = $q.defer();
        this.getAll({
            filters: {
                term: query
            }
        }).then(function(response) {
            deferred.resolve(response.entries);
        });

        return deferred.promise;
    }
});
