(function() {
    'use strict';
    angular.module('core.admin.price').service('PriceTable', /*@ngInject*/ function() {

        this.service = service;

        function service(params) {
            //
            // Default Params
            // 
            this.data = [];

            //
            // Instantiate
            // 
            params = params ? params : {};
            angular.extend(this, params);
        }

        this.service.prototype.addColumn = addColumn;
        this.service.prototype.removeColumn = removeColumn;
        this.service.prototype.removeRow = removeRow;
        this.service.prototype.addRow = addRow;

        // add a column
        function addColumn() {
            //you must cycle all the rows and add a column 
            //to each one
            this.data.forEach(function($row) {
                $row.push({
                    "value": ""
                })
            });
        };

        // remove the selected column
        function removeColumn(index) {
            // remove the column specified in index
            // you must cycle all the rows and remove the item
            // row by row
            this.data.forEach(function(row) {
                row.splice(index, 1);

                //if no columns left in the row push a blank array
                if (row.length === 0) {
                    row.data = [];
                }
            });
        };

        // remove the selected row
        function removeRow(index) {
            // remove the row specified in index
            this.data.splice(index, 1);
            // if no rows left in the array create a blank array
            if (this.data.length === 0) {
                this.data = [];
            }
        };

        //add a row in the array
        function addRow() {
            // create a blank array
            var newrow = [];

            // if array is blank add a standard item
            if (this.data.length === 0) {
                newrow = [{
                    'value': ''
                }];
            } else {
                // else cycle thru the first row's columns
                // and add the same number of items
                this.data[0].forEach(function(row) {
                    newrow.push({
                        'value': ''
                    });
                });
            }
            // add the new row at the end of the array 
            this.data.push(newrow);
        };
    });
})();
