(function() {
    'use strict';
    angular.module('core.user').provider('$userAuth',
        /**
         * @ngdoc object
         * @name core.user.$userProvider
         **/
        /*@ngInject*/
        function $userAuthProvider() {

            this.$get = this.get = function() {
                return {}
            }

            this.loginRequired = function($auth, $state, $timeout, $q) {
                if (!$auth.isAuthenticated()) {
                    $timeout(function() {
                        $state.go('app.login');
                    });
                    return $q.reject('Usuário não está logado. Redirecionando para página de login.');
                } else {
                    return true;
                }
            }
        });
})();
